import { Link, Outlet, useNavigate, useParams } from "react-router-dom";
import { Suspense, useEffect, useState } from "react";
import fetchMovies from "components/services/PixabayAPI";
import { Container, Hr, Image } from "./../MovieDetails/MovieDetails.styled";
// import styled from "styled-components";

const MovieDetails = () => {
  const { movieId } = useParams();
  const [movieData, setMovieData] = useState(null);
  const navigate = useNavigate();
  // useEffect(() => {
  //   // fetch(...)
  // }, [movieId]);

  useEffect(() => {
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [movieId]);

  async function fetchData() {
    try {
      const data = await fetchMovies("movie", movieId);
      console.log("data: ", data);
      setMovieData(data);
      // const image = await fetchMovies('images', movieId);
      // setMovieImg(image);
      // console.log(image);
    } catch (error) {
      console.error(error.message);
    } finally {
      console.log("movieData: ", movieData);
    }
  }

  //   const Image = styled.img`
  //   vertical-align: text-top;
  //   height: 2.5rem;
  // `

  //   const Image = styled.img`
  //   vertical-align: text-top;
  // `;
  // var posterFullUrl = "https://image.tmdb.org/t/p/w185//" + item.poster_path;
  // https://api.themoviedb.org/3/movie/872585//fm6KqXpk3M2HVveHwCrBSSBaO0V.jpg

  //   const Container = styled.div`
  //    display: flex;
  //    align-items: flex-start;
  //    justify-content: flex-start;
  //    gap: 12px;
  // `;

  const moveBack = () => navigate("/");

  return (
    <main>
      {movieData && (
        <>
          <Suspense fallback={<div>Loading page...</div>}>
            <button onClick={moveBack}>Go back</button>
            <Container>
              <Image
                src={`https://image.tmdb.org/t/p/w185/${movieData.poster_path}`}
                alt=""
              />
              <div>
                <h2>
                  Title: {movieData.title}{" "}
                  ({movieData.release_date.slice(0, 4)})
                </h2>
                <p>User Score: {movieData.vote_average}</p>
                <h3>Overview</h3>
                <p>{movieData.overview}</p>
                <h4>Genres</h4>
                <p>
                  {movieData.genres.map((item) => {
                    return <span key={item.id}>{item.name}</span>;
                  })}
                </p>
              </div>
            </Container>
          </Suspense>
        </>
      )}
      <Hr />
      <p>Additional information</p>
      <ul>
        <li key={1}>
          <Link to="cast">Cast</Link>
        </li>
        <li key={2}>
          <Link to="reviews">Reviews</Link>
        </li>
      </ul>

      <Hr />
      <Suspense fallback={<div>Loading page...</div>}>
        <Outlet />
      </Suspense>
    </main>
  );
};

export default MovieDetails;
